<template>
    <table class="standart-table">
        <thead>
            <tr class="standart-table__row">
                <th
                    class="standart-table__header"
                    v-for="th of tableHeaders"
                    :key="th"
                >
                    {{th}}
                </th>                
            </tr>
        </thead>

        <tbody class="standart-table__body">
            <OrdersListItem
                v-for="order of orders"
                :key="order.id"
                :order="order"
            />
        </tbody>
    </table>
</template>
<script>
import OrdersListItem from './OrdersListItem'

export default {
    components: {
        OrdersListItem
    },
    props: ['orders'],
    data: () => ({
        tableHeaders: [
            'ID', 'Покупатель', 'Статус', 'Сумма', 'Дата создания', 'Действия'
        ]
    })
}
</script>