<template>
    <section>
        <Breadcrumbs :breadcrumbs="breadcrumbs"/>

        <div class="content-container" v-if="orders">
            <OrdersList
                :orders="orders"
            />
        </div>
        
        <div class="no-items" v-else>
            Нет заказов
        </div>

        <Loader v-if="loading"/>
    </section>
</template>

<script>
import Breadcrumbs from '@/components/admin/layout/Breadcrumbs'
import OrdersList from '@/components/admin/orders/OrdersList'
import {mapGetters, mapActions} from 'vuex'

export default {
    components: {OrdersList, Breadcrumbs},
    metaInfo(){
        return {
            title: this.$title('Заказы')
        }
    },
    data: () => ({
        loading: true,
        breadcrumbs: {
            title: 'Заказы',
            current: 'Заказы'
        }
    }),
    computed: mapGetters(['orders']),
    methods: mapActions(['fetchOrders']),
    async mounted(){
        await this.fetchOrders()
        this.loading = false
    }
}
</script>