<template>
    <tr class="standart-table-row" :class="statusClass">
        <td class="standart-table__cell">{{order.id}}</td>

        <td class="standart-table__cell">{{order.personName}} {{order.personSurname}}</td>

        <td class="standart-table__cell">{{statusValue}}</td>

        <td class="standart-table__cell">{{order.price}}</td>

        <td class="standart-table__cell">{{order.created}}</td>

        <td class="standart-table__cell">
            <router-link
                class="standart-table__link--view"
                title="Посмотреть"
                to="#"
            >
                <i class="standart-table__icon fas fa-eye"></i>
            </router-link>

            <button
                class="standart-table__btn--delete"
                title="Удалить из БД"
                type="button"
                @click="del">

                <i class="standart-table__icon fas fa-times"></i>
            </button>
        </td>
    </tr>
</template>

<script>
import {mapActions} from 'vuex'

export default {
    props: ['order'],
    computed: {
        statusValue(){
            if(this.order.status == 0){
                return 'Новый'
            }else if(this.order.status == 1){
                return 'Оплачен'
            }else if(this.order.status == 2){
                return 'Доставлен'
            }else{
                return ''
            }
        },
        statusClass(){
            if(this.order.status == 0){
                return 'new'
            }else if(this.order.status == 1){
                return 'paid'
            }else if(this.order.status == 2){
                return 'delivered'
            }else{
                return ''
            }
        }
    },
    methods: {
        ...mapActions(['deleteFromDB', 'changeStatus']),
        del(){
            alert('На данный момент удаление невозможно.')
        }
    }
}
</script>